html {
  scroll-behavior: smooth;
}

#resizeImagesDiv {
  display: none;
}

#noMoreImgs {
  color: red;
  text-align: center;
  margin-bottom: 20px;
}

.firstPageMainDiv {
  height: 100vh;
  width: 100vw;
  position: relative;
  display: inline-block;
}

.firstPageMainDiv .backGroundImg {
  height: 100vh;
  width: 100vw;
  z-index: -1;
  margin-bottom: -5px;
}

.firstPageMainDiv .contentDiv {
  position: absolute;
  z-index: 1;
  margin: 0 auto;
  left: 30%;
  top: 30%;
  font-family: Arial,sans-serif;
  color: #fff;
  width: 35%;
}

.firstPageMainDiv .contentDiv .searchInput__form {
  display: flex;
  align-items: center;
  height: 40px;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__lupeBtn {
  height: 100%;
  background-color: white;
  outline: none;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__lupeBtn .lupeIcon {
  width: 14px;
  height: 14px;
  padding-left: 5px;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__lupeBtn .lupeIcon:hover {
  width: 13px;
  height: 13px;
}

.firstPageMainDiv .contentDiv .searchInput__form input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  display: none;
}

.firstPageMainDiv .contentDiv .searchInput__form input::-webkit-calendar-picker-indicator {
  visibility: hidden;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__input {
  width: 100%;
  height: 100%;
  background-color: white;
  outline: none;
  border: none;
  font-size: 16px;
  padding-left: 10px;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__clearBtn {
  height: 100%;
  background-color: white;
  outline: none;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__clearBtn .crossIcon {
  width: 11px;
  height: 11px;
  padding-right: 5px;
}

.firstPageMainDiv .contentDiv .searchInput__form .searchInput__form__clearBtn .crossIcon:hover {
  width: 10px;
  height: 10px;
}

.secondPageMainDiv {
  width: 70vw;
  margin: auto;
  position: relative;
}

.secondPageMainDiv .arrowBack {
  position: absolute;
  top: 0%;
  left: 0%;
  height: 30px;
  width: 30px;
  padding-top: 4px;
  cursor: pointer;
  z-index: 9999;
}

.secondPageMainDiv .arrowUpHolder {
  position: sticky;
  top: 2%;
  float: right;
  height: 40px;
  width: 40px;
  padding-top: 2px;
  cursor: pointer;
  margin-right: 10px;
  margin-left: -10px;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.3);
  z-index: 9999;
  border: none;
}

.secondPageMainDiv .arrowUpHolder:hover {
  background-color: white;
}

.secondPageMainDiv .arrowUpHolder .arrowUp {
  height: 30px;
  width: 30px;
}

.secondPageMainDiv .searchInput__form {
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 35px;
}

.secondPageMainDiv .searchInput__form .searchInput__form__lupeBtn {
  border: none;
  outline: none;
  background-color: #e4e4e4;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  height: 100%;
}

.secondPageMainDiv .searchInput__form .searchInput__form__lupeBtn .lupeIcon {
  height: 12px;
  width: 12px;
  padding-left: 10px;
}

.secondPageMainDiv .searchInput__form .searchInput__form__lupeBtn .lupeIcon:hover {
  height: 11.5px;
  width: 11.5px;
}

.secondPageMainDiv .searchInput__form input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  display: none;
}

.secondPageMainDiv .searchInput__form input::-webkit-calendar-picker-indicator {
  visibility: hidden;
}

.secondPageMainDiv .searchInput__form .searchInput__form__input {
  margin-left: 0px;
  background-color: #e4e4e4;
  outline: none;
  border: transparent;
  height: 100%;
  font-size: 15px;
  width: 50%;
  padding-left: 5px;
}

.secondPageMainDiv .searchInput__form .searchInput__form__clearBtn {
  align-items: center;
  border: none;
  outline: none;
  background-color: #e4e4e4;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  height: 100%;
}

.secondPageMainDiv .searchInput__form .searchInput__form__clearBtn .crossIcon {
  height: 10px;
  width: 10px;
  padding-right: 10px;
}

.secondPageMainDiv .searchInput__form .searchInput__form__clearBtn .crossIcon:hover {
  height: 8.5px;
  width: 8.5px;
}

.secondPageMainDiv .alertDiv {
  width: 55%;
  margin: auto;
}

.secondPageMainDiv .alertDiv .alertP {
  color: red;
  display: flex;
  align-items: center;
  width: 300px;
}

.secondPageMainDiv .secondPage__h1 {
  font-size: 40px;
}

.secondPageMainDiv .mainTagsDiv {
  position: relative;
}

.secondPageMainDiv .mainTagsDiv .leftArrowHolder {
  position: absolute;
  height: 42px;
  width: 40px;
  display: flex;
  align-items: center;
  visibility: hidden;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.6);
}

.secondPageMainDiv .mainTagsDiv .leftArrowHolder .leftArrow {
  border: solid black;
  border-width: 3px 0px 0px 3px;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transition: fill .1s ease-in-out;
  width: 5px;
  height: 5px;
  margin-left: 15px;
  cursor: pointer;
  opacity: 0.3;
}

.secondPageMainDiv .mainTagsDiv .leftArrowHolder .leftArrow:hover {
  opacity: 1;
}

.secondPageMainDiv .mainTagsDiv .rightArrowHolder {
  position: absolute;
  height: 42px;
  width: 40px;
  display: flex;
  visibility: hidden;
  align-items: center;
  backface-visibility: hidden;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.6);
  right: 0;
}

.secondPageMainDiv .mainTagsDiv .rightArrowHolder .rightArrow {
  border: solid black;
  border-width: 3px 0px 0px 3px;
  display: inline-block;
  padding: 3px;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
  transition: fill .1s ease-in-out;
  width: 5px;
  height: 5px;
  margin-left: 15px;
  cursor: pointer;
  opacity: 0.3;
}

.secondPageMainDiv .mainTagsDiv .rightArrowHolder .rightArrow:hover {
  opacity: 1;
}

.secondPageMainDiv .mainTagsDiv .innerTagsDiv {
  display: inline-block;
  margin-bottom: 30px;
  margin-left: 0px;
  position: relative;
  scroll-behavior: smooth;
  white-space: nowrap;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 100%;
}

.secondPageMainDiv .mainTagsDiv .innerTagsDiv .tagHolder {
  border: 1px solid black;
  opacity: 0.3;
  display: inline-block;
  width: 150px;
  max-width: 100%;
  height: 40px;
  border-radius: 4px;
  color: black;
  margin-right: 5px;
  margin-left: 5px;
  text-align: center;
  cursor: pointer;
}

.secondPageMainDiv .mainTagsDiv .innerTagsDiv .tagHolder:hover {
  opacity: 1;
}

.secondPageMainDiv .mainTagsDiv .innerTagsDiv .tagHolder .tagP {
  margin-top: 9px;
}

.secondPageMainDiv .secondPage__imagesContainer {
  width: 100%;
  min-height: 90vh;
  display: flex;
  justify-content: space-between;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv {
  margin: auto;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv .resizeImageHolder {
  width: 100%;
  margin-bottom: 4.5%;
  position: relative;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv .resizeImageHolder .resize__onImg__innerInfoDiv {
  position: absolute;
  display: none;
  align-items: center;
  bottom: 0;
  padding: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv .resizeImageHolder .resize__onImg__innerInfoDiv .onImg__profile_photo {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-left: 5px;
  margin-right: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv .resizeImageHolder .resize__onImg__innerInfoDiv .onImg__userInfoDiv .onImg__nameP {
  color: white;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv .resizeImageHolder .resizeImg {
  width: 100%;
}

.secondPageMainDiv .secondPage__imagesContainer .resizeImagesDiv .resizeImageHolder .resizeImg:hover {
  cursor: pointer;
  filter: brightness(80%);
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 32%;
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv .leftImageHolder {
  width: 100%;
  margin-bottom: 4.5%;
  position: relative;
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv .leftImageHolder .onImg__innerInfoDiv {
  position: absolute;
  display: none;
  align-items: center;
  bottom: 0;
  padding: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv .leftImageHolder .onImg__innerInfoDiv .onImg__profile_photo {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-left: 5px;
  margin-right: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv .leftImageHolder .onImg__innerInfoDiv .onImg__userInfoDiv .onImg__nameP {
  color: white;
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv .leftImageHolder .leftImg {
  width: 100%;
}

.secondPageMainDiv .secondPage__imagesContainer .leftImagesDiv .leftImageHolder .leftImg:hover {
  cursor: pointer;
  filter: brightness(80%);
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  width: 32%;
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv .midImageHolder {
  width: 100%;
  margin-bottom: 4.5%;
  position: relative;
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv .midImageHolder .onImg__innerInfoDiv {
  position: absolute;
  display: none;
  align-items: center;
  bottom: 0;
  padding: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv .midImageHolder .onImg__innerInfoDiv .onImg__profile_photo {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-left: 5px;
  margin-right: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv .midImageHolder .onImg__innerInfoDiv .onImg__userInfoDiv .onImg__nameP {
  color: white;
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv .midImageHolder .midImg {
  width: 100%;
}

.secondPageMainDiv .secondPage__imagesContainer .midImagesDiv .midImageHolder .midImg:hover {
  cursor: pointer;
  filter: brightness(80%);
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 32%;
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv .rightImageHolder {
  width: 100%;
  margin-bottom: 4.5%;
  position: relative;
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv .rightImageHolder .onImg__innerInfoDiv {
  position: absolute;
  display: none;
  align-items: center;
  bottom: 0;
  padding: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv .rightImageHolder .onImg__innerInfoDiv .onImg__profile_photo {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-left: 5px;
  margin-right: 10px;
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv .rightImageHolder .onImg__innerInfoDiv .onImg__userInfoDiv .onImg__nameP {
  color: white;
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv .rightImageHolder .rightImg {
  width: 100%;
}

.secondPageMainDiv .secondPage__imagesContainer .rightImagesDiv .rightImageHolder .rightImg:hover {
  cursor: pointer;
  filter: brightness(60%);
}

.openedImageMainDiv {
  position: fixed;
  height: 100%;
  width: 100vw;
  top: 0px;
  left: 0px;
  border: none;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9999;
}

.openedImageMainDiv .modalCancelBtn {
  height: 15px;
  width: 15px;
  padding-top: 15px;
  padding-left: 15px;
  cursor: pointer;
}

.openedImageMainDiv .outerPicHolder {
  display: flex;
  align-items: center;
  justify-content: center;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder {
  top: 5%;
  left: 9.5%;
  width: 80vw;
  background-color: white;
  z-index: 1;
  border: 0.1px solid black;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv {
  display: flex;
  align-items: center;
  height: 80px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .innerInfoDiv {
  display: flex;
  align-items: center;
  height: 100%;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .innerInfoDiv .profile_photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 15px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .innerInfoDiv .userInfoDiv {
  display: flex;
  flex-direction: column;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .innerInfoDiv .userInfoDiv .nameP {
  margin-bottom: 2px;
  font-size: 16px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .innerInfoDiv .userInfoDiv .usernameP {
  margin-top: 0px;
  font-size: 13px;
  margin-bottom: 20px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .locationDiv {
  display: flex;
  margin-left: auto;
  align-items: center;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .locationDiv .locationP {
  margin-right: 5px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .mainInfoDiv .locationDiv .locationIcon {
  height: 20px;
  width: 20px;
  margin-right: 10px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .outerPicDiv {
  height: 70vh;
  display: flex;
  align-items: center;
  margin: auto;
  margin-bottom: 40px;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .outerPicDiv .picInnerDiv {
  max-height: 100%;
  margin: auto;
  display: flex;
  justify-content: center;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .outerPicDiv .picInnerDiv .pic {
  max-width: 100%;
  object-fit: contain;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .resize__locationDiv {
  margin-left: auto;
  align-items: center;
  margin-left: 15px;
  margin-top: -15px;
  display: none;
}

.openedImageMainDiv .outerPicHolder .innerPicHolder .resize__locationDiv .resize__location_icon {
  height: 20px;
  width: 20px;
  margin-right: 5px;
}

@media only screen and (max-width: 1500px) {
  #innerPicHolder {
    width: 90vw;
  }
}

@media only screen and (max-width: 1200px) {
  #contentDiv {
    left: 25%;
    width: 50%;
  }
}

@media only screen and (max-width: 1000px) {
  #secondPageMainDiv {
    width: 90vw;
  }
  #contentDiv {
    left: 15%;
    width: 70%;
  }
  #resizeImagesDiv {
    display: block;
  }
  #leftImagesDiv {
    display: none;
  }
  #midImagesDiv {
    display: none;
  }
  #rightImagesDiv {
    display: none;
  }
  #innerPicHolder {
    width: 85vw;
  }
  #picVertical {
    max-width: 80%;
  }
  #resize__locationDiv {
    display: flex;
  }
  #locationDiv {
    display: none;
  }
  #mainTagsDiv {
    display: none;
  }
}

@media only screen and (max-width: 450px) {
  #arrowBack {
    padding-top: 5px;
    height: 25px;
    width: 25px;
  }
}
